.App-header {
  background-color: #ebebeb;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 20px;
  text-align: center;
  color: white;
  overflow-x: hidden;
}
.App-header img{
  width: 100%;
}
@media screen and (max-width: 700px){
  .App-header img{
    width: auto;
    height: 100vh;
  }
}