
.wrapper-carousel{
  padding-bottom: 40px;
}
.wrapper-carousel h2{
  text-transform: uppercase;
  text-align: center;
}
.carouselItem{
  text-align: center;
  display: inline-flex !important;
  align-items: center;
  justify-content: center;
  height: 100px;
}
.carouselItem img{
  max-width: 200px;
  max-height: 100px;
  display: inline-block;
}

@media screen and (max-width: 700px) {
  .wrapper-carousel h2{
    font-size: 14px;
  }
}